            </button>

            <div class="counter">
                <span class="label">–î–æ —Å–ª–µ–¥—É—é—â–µ–≥–æ —Ä–∞—É–Ω–¥–∞: </span>
                <span class="value">{{ countdown }}</span>
            </div>

            <!-- <button class="test-button" @click="clearData()">
                –û–ß–ò–°–¢–ò–¢–¨ –î–ê–ù–ù–´–ï
            </button>

            <button class="test-button" @click="executeRound()">
                –í–´–ü–û–õ–ù–ò–¢–¨ –†–ê–£–ù–î
            </button> -->
