<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal buy-bid-modal" @click.stop="">
            <!-- <div class="new-modal-back-button-container"> -->
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            <!-- </div> -->
        <div class="modal-content">
        <div class="modal-content-outer-container">
        <div class="modal-content-container">
        <p class="window-title">{{$localizeFilter(isEditing ? 'changing_application_for_auction' : 'creating_application_for_auction')}}</p>

        <p class="window-text">{{$localizeFilter('buy_bid_modal_text_amount', ['USDT'])}}</p>
        <div class="input-field">
            <input id="amount" class="input-class" :placeholder="$localizeFilter('amount_in', ['USDT'])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
            <div class="errors">
                <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                    {{ error.$message }}
                </small>
            </div>
        </div>

        <div class="test-balance">{{$localizeFilter('available_for_deposit', [$utils.formatNumber(maxAmount, 'en-US', 0, 6) +' USDT'])}}</div>

        <p class="window-text">{{$localizeFilter('buy_bid_modal_text_price', ['USDT'])}}</p>
        <div class="input-field">
            <input id="payAmount" class="input-class" :placeholder="$localizeFilter('price_in', ['USDT'])" type="text" v-model.trim="payAmount"  :class="{ invalid: v$.payAmount.$error }"/>
            <div class="errors">
                <small class="error" v-for="(error, index) of v$.payAmount.$errors" :key="index">
                    {{ error.$message }}
                </small>
            </div>
        </div>
        
        <div class="modal-warning">
            <img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
            <p class="window-text warning">{{$localizeFilter('buy_bid_modal_warning')}}</p>
        </div>

        <button class="basic-button submit-button" @click="submitHandler" :class="submitInProgress ? 'inactive' : ''">{{$localizeFilter(isEditing ? 'change_an_application' : 'place_an_application')}}</button>
    </div>
    </div>
    </div>
    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, maxValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        bid: {
            type: Object,
            default: null
        },
        poolId: {
            type: Number,
            required: true
        },
        testBalance: {
            type: Number,
            required: true
        }
    },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
                // maxValue: helpers.withMessage(localizeFilter('not_enough_funds_on_balance',['USDT']), maxValue(this.maxAmount)),
            },
            payAmount: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
                maxValue: helpers.withMessage(localizeFilter('not_enough_funds_on_balance',['USDT']), maxValue(this.maxAmount)),
            },
        }
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        amount: null,
        payAmount: null,
        minAmount: 0,
        submitInProgress: false,
    }),
    methods: {
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }

            this.submitInProgress = true
            // send data to store
            //   let amount = multiplyNumberByDecimals(this.amount, this.assetDecimalsMap[this.bid.leftCoinTicker]).toString();
            const amount = Math.floor(Number(this.amount) * 1000000)
            const payAmount = Math.floor(Number(this.payAmount) * 1000000)
            if (amount > payAmount) {
                this.$error({message: localizeFilter('error_amount_more_than_pay_amount')})
                return
            }
            const data = {poolId: this.poolId, requestId: this.bid?.requestId, amount: amount, payAmount: payAmount}
            try {
                if (this.isEditing) {
                    await this.$store.dispatch('editLiquidityProvidingAuctionBuyRequest', data)
                    this.$message(localizeFilter('change_bid_successful'))
                    this.$emit('reloadData')
                    this.$emit('closeModal')
                    setTimeout(()=>{
                        this.submitInProgress = false
                    },1000)
                } else {
                    await this.$store.dispatch('createLiquidityProvidingAuctionBuyRequest', data)
                    this.$message(localizeFilter('buy_bid_successful'))
                    this.$emit('reloadData')
                    this.$emit('closeModal')
                    setTimeout(()=>{
                        this.submitInProgress = false
                    },1000)
                }
            } catch(e) {
                this.$error(e)
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 
        },
    },
    computed: {
        isEditing() {
            return this.bid !== null;
        },
        assetDecimalsMap() {
            return this.$store.getters.assetDecimalsMap;
        },
        maxAmount() {
            return this.testBalance / 1000000.0
        }
    },
    mounted() {
        if (this.bid) {
            this.amount = this.bid.amount / 1000000.0
            this.payAmount = this.bid.payAmount / 1000000.0
        }
    }
})
</script>


